@tailwind base;
@tailwind components;
@tailwind utilities;

/* Fonts */
@font-face {
  font-family: bookmanOldStyleLeve;
  src: url(./fonts/Bookman-Old-Style.TTF);
}

@font-face {
  font-family: brushScriptStdMedium;
  src: url(./fonts/Brush-Script-Std.otf);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  font-size: 62.5%;
}

html, body {
  font-family: 'Roboto', sans-serif;
  font-size: 1.6rem;

  /* background-color: rgb(90, 89, 99);
  color: rgb(167, 233, 227); */

  align-items: center;
  display: flex;
  flex-direction: column;
  width: 99vw;
}

header {
  align-items: center;
  display: flex;
  flex-direction: row;
  font-family: brushScriptStdMedium;
  font-size: 3.2rem;
  justify-content: center;
  text-align: center;
  width: 99vw;
}

header .menuSelect {
  background-color: #d3d3d3;
  border: 1px #d3d3d3 solid;
  border-bottom-right-radius: 20px;
  border-top-left-radius: 20px;
  margin-right: 20px;
  padding: 10px;
  font-size: 24px;
}

header .menuSelect:hover {
  border: 1px gray solid;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: gray;
}

main {
  align-items: center;
  display: flex;
  flex-direction: row;
  background-color: lightgray;
  border: 4px solid lightgray;
  border-bottom-right-radius: 20px;
  border-top-left-radius: 20px;
  /*font-family: brushScriptStdMedium;*/
  font-family: bookmanOldStyleRegular;
  font-size: 28px;

  margin-top: 10px;
  text-align: justify;
  width: 900px;
}

main .conteudo {
  background-color: lightgray;
  border: 4px solid lightgray;
  border-bottom-right-radius: 20px;
  border-top-left-radius: 20px;
  /*font-family: brushScriptStdMedium;*/
  font-family: bookmanOldStyleRegular;
  font-size: 28px;
  height: auto;
  text-align: justify;
  text-transform: none;

  justify-content: center;
}
